import React from 'react';
import { Container, Header, Wrapper, Text, Coin } from './style';
import { Link } from 'react-router-dom';
import { fetchCoinList } from '../../api';
import { useQuery } from 'react-query';
import { ICoinListDataType } from './index.d';
import { DoubleArrowOutlined } from '@mui/icons-material';
import Loading from '../Loading';
const imageSrc = 'https://coinicons-api.vercel.app/api/icon/';

const CoinList = () => {
    const { isLoading, data } = useQuery<ICoinListDataType[]>(
        ['allcoin'],
        fetchCoinList,
    );

    return (
        <Container>
            <Header headLine="Coin List" />
            {isLoading && <Loading />}
            <Wrapper as={'ul'}>
                {data?.slice(0, 100)?.map((coin) => (
                    <Coin key={coin.id}>
                        <Link
                            to={`${process.env.PUBLIC_URL}/${coin.id}`}
                            state={{
                                name: coin.name,
                                symbol: coin.symbol.toLowerCase(),
                            }}
                        >
                            <Wrapper flexDirection="row" alignItems="center">
                                <img
                                    alt={coin.symbol + 'image'}
                                    src={imageSrc + coin.symbol.toLowerCase()}
                                />
                                <Text tag="p">{coin.name}</Text>
                                <DoubleArrowOutlined />
                            </Wrapper>
                            <Wrapper flexDirection="column">
                                <Text tag="p">
                                    {coin.symbol} Rank is
                                    <strong> {coin.rank}</strong>
                                </Text>
                                <Text tag="span" fontSize="1.2em">
                                    Click here for more information
                                </Text>
                            </Wrapper>
                        </Link>
                    </Coin>
                ))}
            </Wrapper>
        </Container>
    );
};

export default CoinList;
