import React from 'react';
import ApexCharts from 'react-apexcharts';
import { useQuery } from 'react-query';
import Loading from '../Loading';
import { fetchCoinOHLCV } from '../../api';
import { ChartProps, IHistoryData } from '../Chart/index';
import { WrapperPrice } from './style';

export default function Price({ coinId }: ChartProps) {
    const { isLoading, data: history } = useQuery<IHistoryData[]>(
        ['ohlcv'],
        () => fetchCoinOHLCV(coinId as string),
    );

    return (
        <WrapperPrice>
            <h2>Price</h2>
            {isLoading ? (
                <Loading />
            ) : history && Array.isArray(history) ? (
                <ApexCharts
                    type="line"
                    series={[
                        {
                            name: 'Price',
                            data: history.map(({ close }) =>
                                Number(Number(close).toFixed(3)),
                            ),
                        },
                    ]}
                    options={{
                        chart: {
                            toolbar: {
                                show: false,
                            },
                            zoom: {
                                enabled: false,
                            },
                        },
                        tooltip: {
                            y: {
                                formatter: (value) => `$${value.toFixed(2)}`,
                            },
                        },
                        xaxis: {
                            type: 'datetime',
                            categories: history?.map(
                                ({ time_close }) => time_close,
                            ),
                        },
                        stroke: {
                            curve: 'smooth',
                            width: 3,
                        },
                    }}
                />
            ) : (
                <div>Sorry not data</div>
            )}
        </WrapperPrice>
    );
}
