const BASE_URL = 'https://api.coinpaprika.com/v1/';

function get(url: string) {
    return async (param?: string) => {
        if (!param) param = '';
        try {
            const result = await (await fetch(url + param)).json();
            return result;
        } catch (e) {
            console.error('Fetch Error');
            console.error(e);
            return [];
        }
    };
}

const COIN_URL = BASE_URL + 'coins/';
const TICKER_URL = BASE_URL + 'tickers/';
const getCoin = get(COIN_URL);
export function fetchCoinList() {
    return getCoin();
}
export function fetchCoinInfo(coinId: string | undefined) {
    return getCoin(coinId);
}
const getTicker = get(TICKER_URL);
export function fetchTickerInfo(coinId: string | undefined) {
    return getTicker(coinId);
}

// chart
const BASE_OHLCV_URL = 'https://ohlcv-api.nomadcoders.workers.dev?coinId=';
export function fetchCoinOHLCV(coinId: string) {
    return get(BASE_OHLCV_URL)(coinId);
}
