import React from 'react';
import ApexCharts from 'react-apexcharts';
import { fetchCoinOHLCV } from '../../api';
import { useQuery } from 'react-query';
import Loading from '../Loading';
import { WrapperChart } from './style';

export interface ChartProps {
    coinId: string | undefined;
}
export interface IHistoryData {
    close: string;
    high: string;
    low: string;
    open: string;
    volume: string;
    market_cap: number;
    time_close: number;
    time_open: number;
}

export default function Chart({ coinId }: ChartProps) {
    const { isLoading, data: history } = useQuery<IHistoryData[]>(
        ['ohlcv'],
        () => fetchCoinOHLCV(coinId as string),
    );

    return (
        <WrapperChart>
            <h2>Chart</h2>
            {isLoading ? (
                <Loading />
            ) : history && Array.isArray(history) ? (
                <ApexCharts
                    type="candlestick"
                    series={[
                        {
                            data: history.map(
                                ({ time_open, open, high, low, close }) => {
                                    return {
                                        x: new Date(time_open),
                                        y: [open, high, low, close].map((v) =>
                                            Number(v),
                                        ),
                                    };
                                },
                            ),
                        },
                    ]}
                    options={{
                        chart: {
                            toolbar: {
                                show: false,
                            },

                            zoom: {
                                enabled: false,
                            },
                        },

                        xaxis: {
                            type: 'datetime',
                        },
                        plotOptions: {
                            candlestick: {
                                colors: {
                                    upward: '#f9541e    ',
                                    downward: '#3c5ceb',
                                },
                                wick: {
                                    useFillColor: true,
                                },
                            },
                        },
                    }}
                />
            ) : (
                <div>Sorry not data</div>
            )}
        </WrapperChart>
    );
}
