import React from 'react';
import YouTube from 'react-youtube';
import {
    useLocation,
    useParams,
    Routes,
    Route,
    Link,
    useMatch,
} from 'react-router-dom';
import { Container } from '../CoinList/style';
import {
    Header,
    Content,
    WrapperBox,
    WrapperTags,
    Wrapper,
    Text,
    WrapperTap,
    LinkItem,
    WrapperDescription,
} from './style';
import HomeIcon from '@mui/icons-material/Home';
import NewReleasesIcon from '@mui/icons-material/NewReleases';

import { fetchCoinInfo, fetchTickerInfo } from '../../api/index';
import { useQuery } from 'react-query';
import { ICoinInfoData, ICoinTickerDataType } from './index.d';
import Loading from '../Loading';
import Chart from '../Chart';
import Price from '../Price';
import { Helmet } from 'react-helmet-async';
const imageSrc = 'https://coinicons-api.vercel.app/api/icon/';

interface RouteState {
    name: string;
    symbol: string;
}
function Coin() {
    const state = useLocation().state as RouteState;
    const { coinId } = useParams();
    const { isLoading: infoLoading, data: infoData } = useQuery<ICoinInfoData>(
        ['info', coinId],
        () => fetchCoinInfo(coinId),
    );

    const matchChart = useMatch(
        `/${process.env.PUBLIC_URL}` + '/:coinId/chart',
    );
    const matchPrice = useMatch(
        `/${process.env.PUBLIC_URL}` + '/:coinId/price',
    );

    const { isLoading: tickerLoading, data: tickerData } =
        useQuery<ICoinTickerDataType>(
            ['ticker', coinId],
            () => fetchTickerInfo(coinId),
            {
                refetchInterval: 5000,
            },
        );

    return (
        <Container>
            <Helmet>
                <title>
                    {state?.name
                        ? state.name
                        : !infoLoading || !tickerLoading
                        ? infoData?.name || tickerData?.name
                        : ''}
                </title>
            </Helmet>
            <LinkItem>
                <HomeIcon />
                <Link to={`${process.env.PUBLIC_URL}/`}>Home</Link>
            </LinkItem>
            <Header
                headLine={
                    state?.name
                        ? state.name
                        : !infoLoading || !tickerLoading
                        ? infoData?.name || tickerData?.name
                        : ''
                }
            >
                {infoData?.is_new && <NewReleasesIcon />}
                {(state?.symbol || infoData?.symbol || tickerData?.symbol) && (
                    <img
                        src={
                            imageSrc +
                            (state?.symbol || infoData?.symbol.toLowerCase())
                        }
                        alt={state?.symbol + 'image'}
                    />
                )}
            </Header>

            {infoLoading || tickerLoading ? (
                <Loading />
            ) : (
                <Content>
                    <WrapperTags>
                        {infoData?.tags?.map((v) => (
                            <Text tag="p" key={v.id}>
                                #{v.name}
                            </Text>
                        ))}
                    </WrapperTags>
                    <WrapperBox>
                        <Wrapper>
                            <Text tag="p">Rank</Text>
                            <Text tag="p">{infoData?.rank}</Text>
                        </Wrapper>
                        <Wrapper>
                            <Text tag="p">Percent</Text>
                            <Text tag="p" color={getColor(tickerData)}>
                                {tickerData?.quotes.USD.percent_change_24h}
                            </Text>
                        </Wrapper>
                        <Wrapper>
                            <Text tag="p">Price</Text>
                            <Text tag="p" color={getColor(tickerData)}>
                                ${tickerData?.quotes.USD.price.toFixed(2)}
                            </Text>
                        </Wrapper>
                    </WrapperBox>
                    <WrapperDescription>
                        {infoData && <GetYoutube infoData={infoData} />}
                        <Text tag="p">
                            {infoData?.description || 'Sorry No Description'}
                        </Text>
                    </WrapperDescription>
                    <WrapperBox justifyContent="space-around">
                        <Wrapper>
                            <Text tag="p">totalsuply</Text>
                            <Text tag="p">{tickerData?.total_supply}</Text>
                        </Wrapper>
                        <Wrapper>
                            <Text tag="p">maxsuply</Text>
                            <Text tag="p">{tickerData?.max_supply}</Text>
                        </Wrapper>
                    </WrapperBox>
                    <WrapperTap>
                        <LinkItem isActive={!!matchChart}>
                            <Link to={'chart'}>CHART</Link>
                        </LinkItem>
                        <LinkItem isActive={!!matchPrice}>
                            <Link to={'price'}>PRICE</Link>
                        </LinkItem>
                    </WrapperTap>
                    <Routes>
                        <Route
                            path="chart"
                            element={<Chart coinId={infoData?.id} />}
                        />
                        <Route
                            path="price"
                            element={<Price coinId={infoData?.id} />}
                        />
                    </Routes>
                </Content>
            )}
        </Container>
    );
}
interface IYOUTUBE {
    infoData: ICoinInfoData;
}
const GetYoutube: React.FC<IYOUTUBE> = ({ infoData }) => {
    try {
        const youtubeLink = infoData.links.youtube[0];
        const index = youtubeLink.indexOf('v=');
        let youtubeId = youtubeLink
            .slice(index + 2)
            .match(/[0-9A-Za-z]/g)
            ?.join('');
        if (index === -1) {
            youtubeId = youtubeLink
                .slice(youtubeLink.indexOf('youtu.be/') + 9)
                .match(/[0-9A-Za-z]/g)
                ?.join('');
        }

        return <YouTube videoId={youtubeId} />;
    } catch (e) {
        return null;
    }
};
const getColor = (tickerData: ICoinTickerDataType | undefined): string => {
    if (!tickerData) return '';
    const percent = tickerData.quotes.USD.percent_change_24h;
    return percent > 1 ? '#f9541e' : percent === 0 ? 'black' : '#3c5ceb';
};

export default Coin;
