import React from 'react';

interface Props {
    headLine: string | undefined;
    fontSize?: string;
    children?:
        | string
        | React.ReactElement<any, string | React.JSXElementConstructor<any>>
        | React.ReactNode;
    className?: string;
}
const PageHeader: React.FC<Props> = ({ className, headLine, children }) => {
    return (
        <header className={className}>
            <h1>{headLine}</h1>
            {children}
        </header>
    );
};

export default PageHeader;
